import { NavLink, Outlet, useParams, useSearchParams } from "@remix-run/react";
import { cloneElement, useRef } from "react";
import Section from "~/components/Section";
import Tag from "~/components/Tag";
import { Role, RoleFilter, Work, works } from "~/models/works";
import { useWindow } from "~/util/hooks";

export default function Role() {
  const params = useParams();
  const role = params["role"] as Role;
  const [search, setSearch] = useSearchParams();
  const filter = search.get("filter") as RoleFilter<Role> | null;

  const setFilter = (newFilter: RoleFilter<Role>) => {
    search.set("filter", newFilter);
    setSearch(search);
  };

  const frame = useRef<HTMLDivElement>(null);

  const sections: Record<Role, () => JSX.Element> = {
    artist: () => (
      <>
        <div>
          My creative work encompasses{" "}
          <Tag to={() => setFilter("hypertext")}>hypertext</Tag> and{" "}
          <Tag to={() => setFilter("performance")}>performance</Tag>,
          deconstructing speech and writing by placing it in multimedia, coded
          ecosystems.
        </div>
      </>
    ),
    researcher: () => (
      <>
        <div>
          My <Tag to={() => setFilter("teaching")}>teaching</Tag> and{" "}
          <Tag to={() => setFilter("writing")}>writing</Tag> is informed by
          posthumanism and deconstruction, investigating technology and
          intermediality to find new approaches to the human.
        </div>
      </>
    ),
    designer: () => (
      <>
        As a <Tag to={() => setFilter("web")}>web</Tag> and{" "}
        <Tag to={() => setFilter("multimedia")}>multimedia</Tag> designer, I
        create tools for thought and innovative platforms for communication.
      </>
    ),
  };

  const thisWorks = (works[role] as Work<Role>[]).filter(
    (work) => !filter || work.filter === filter
  );

  return (
    <>
      <Section>{sections[role]()}</Section>
      <div
        className="scrollbar-none flex w-full flex-wrap justify-around space-x-2 space-y-2"
        ref={frame}
      >
        {thisWorks.map(({ title, subtitle, route, background }) => (
          <NavLink
            data-matter-label={route}
            key={route}
            to={route + (search ? "?" + search : search)}
            className={({ isActive }) =>
              `relative flex aspect-square w-[300px] max-w-full flex-none flex-col items-center justify-center overflow-hidden rounded-full bg-gradient-to-br from-slate-800 to-slate-900 transition-transform duration-500 hover:z-10 hover:scale-125`
            }
          >
            {cloneElement(background, {
              autoPlay: true,
              muted: true,
              loop: true,
              className:
                "object-cover w-full h-full absolute top-0 left-0 z-0 rounded-full",
            })}
            <div className="relative z-10 rounded-lg bg-black/50 px-1 text-center font-menu text-xl shadow-lg">
              {title}
            </div>
            <div className="relative z-10 rounded-lg bg-black/50 px-1 text-center font-menu text-sm text-gray-200 shadow-lg">
              {subtitle}
            </div>
          </NavLink>
        ))}
      </div>
      <Outlet />
    </>
  );
}
