import {
  Link,
  useLocation,
  useParams,
  useSearchParams,
} from "@remix-run/react";
import invariant from "tiny-invariant";
import { Role, Work, works } from "~/models/works";
import { cloneElement } from "react";

export default function Works() {
  const { role, work: route } = useParams() as { role: Role; work: string };
  const { search } = useLocation();
  const thisWorks = works[role] as Work<Role>[];
  const thisWork = thisWorks.find((work) => work.route === route);
  invariant(thisWork);
  // const workIndex = thisWorks.indexOf(thisWork)

  return (
    <div className="fixed left-0 top-0 z-10 flex h-full w-full items-center justify-center overflow-y-auto bg-black/50 p-8 backdrop-blur-sm">
      <Link
        to={`/${role}` + search}
        className="absolute left-0 top-0 -z-10 h-full w-full opacity-50"
      >
        {cloneElement(thisWork.background, {
          muted: true,
          className: "object-cover w-full h-full absolute top-0 left-0 -z-10",
        })}
      </Link>
      <div className="relative max-h-full max-w-4xl cursor-default overflow-y-auto rounded-lg border border-gray-400 bg-black/20 p-4 backdrop-blur-lg">
        <div className="sticky top-4 z-10 mb-8 text-center text-2xl font-bold drop-shadow-text">
          {thisWork.title}
        </div>
        <div className="text-center">{thisWork.subtitle}</div>
        <div className="my-4">{thisWork.content}</div>
        <div className="my-4 space-y-4 text-base">{thisWork.description}</div>
      </div>
    </div>
  );
}
